import { TrackerConfig } from '../types';
import config from './config';
import consent from './consent';
import visitor from '../modules/visitor';
import events from '../modules/events';
import network from '../utils/network';

// Variable para almacenar la referencia al módulo de experimentos una vez cargado
let experimentsModule: any = null;

/**
 * Initialize the tracking script
 */
function init(userConfig: Partial<TrackerConfig> = {}): void {
  // Initialize configuration
  config.init(userConfig);
  
  // Set up network endpoint
  network.setEndpoint(config.getApiEndpoint());
  
  // Initialize consent manager
  consent.init({
    required: config.isConsentRequired()
  });
  
  // Initialize modules
  visitor.init();
  events.init();
  
  // Initialize experiments if enabled
  if (config.areExperimentsEnabled()) {
    // Cargar el módulo de experimentos de forma diferida
    import(/* webpackChunkName: "experiments" */ '../modules/experiments')
      .then(module => {
        experimentsModule = module.default;
        experimentsModule.init();
        
        if (config.isDebugEnabled()) {
          console.log('Experiments module initialized');
        }
      })
      .catch(error => {
        console.error('Error loading experiments module:', error);
      });
  }
  
  // Log initialization if debug mode is enabled
  if (config.isDebugEnabled()) {
    console.log('Tracking script initialized with config:', config.get());
  }
}

/**
 * Track a page view
 */
function trackPageView(): void {
  events.trackPageView();
}

/**
 * Track an event
 */
function trackEvent(name: string, properties: Record<string, any> = {}): void {
  events.trackEvent(name, properties);
}

/**
 * Run an experiment
 */
async function runExperiment(experimentId: string, experimentConfig: any): Promise<any> {
  // Solo cargar el módulo de experimentos cuando se necesite
  if (config.areExperimentsEnabled()) {
    try {
      // Si ya tenemos el módulo cargado, usarlo directamente
      if (experimentsModule) {
        return experimentsModule.runExperiment(experimentId, experimentConfig);
      }
      
      // Si no, cargarlo bajo demanda
      const module = await import(/* webpackChunkName: "experiments" */ '../modules/experiments');
      experimentsModule = module.default;
      return experimentsModule.runExperiment(experimentId, experimentConfig);
    } catch (error) {
      console.error('Error loading experiments module:', error);
      return null;
    }
  }
  return null;
}

/**
 * Set consent status
 */
function setConsent(granted: boolean, categories?: Record<string, boolean>): void {
  consent.setConsent(granted, categories);
  
  // Re-initialize modules if consent is granted
  if (granted) {
    visitor.init();
    events.init();
    
    if (config.areExperimentsEnabled()) {
      // Cargar y inicializar experimentos si es necesario
      if (experimentsModule) {
        experimentsModule.init();
      } else {
        import(/* webpackChunkName: "experiments" */ '../modules/experiments')
          .then(module => {
            experimentsModule = module.default;
            experimentsModule.init();
          })
          .catch(error => {
            console.error('Error loading experiments module:', error);
          });
      }
    }
  }
}

/**
 * Get visitor ID
 */
function getVisitorId(): string {
  return visitor.getVisitorId();
}

/**
 * Get session ID
 */
function getSessionId(): string {
  return visitor.getSessionId();
}

/**
 * Set a user property
 */
function setUserProperty(key: string, value: any): void {
  visitor.setUserProperty(key, value);
}

/**
 * Get a user property
 */
function getUserProperty(key: string): any {
  return visitor.getUserProperty(key);
}

/**
 * Reset tracking data
 */
function reset(): void {
  visitor.reset();
  events.clearQueue();
  
  // Reset experiments if loaded
  if (experimentsModule) {
    experimentsModule.reset();
  }
}

// Auto-initialize if script tag has data attributes
if (typeof window !== 'undefined') {
  // Use setTimeout to ensure the script runs after the DOM is loaded
  setTimeout(() => {
    init();
  }, 0);
}

// Export public API
export default {
  init,
  trackPageView,
  trackEvent,
  runExperiment,
  setConsent,
  getVisitorId,
  getSessionId,
  setUserProperty,
  getUserProperty,
  reset
}; 